import React from 'react';
import classnames from "classnames";
import styles from "./styles.module.css";
import {FormControl, InputLabel, OutlinedInput} from '@mui/material';
import {DatePicker} from "@mui/x-date-pickers";

function PersonalData() {
    return (
        <form method="post" className={classnames(styles.wrap)}>
            <FormControl>
                <InputLabel required={true} htmlFor="surname"></InputLabel>
                <OutlinedInput required={true} label="" id="surname" name="surname" defaultValue=""/>
            </FormControl>
            <FormControl>
                <InputLabel required={true} htmlFor="name"></InputLabel>
                <OutlinedInput required={true} label="" id="name" defaultValue=""/>
            </FormControl>
            <FormControl>
                <InputLabel htmlFor="patronymic"></InputLabel>
                <OutlinedInput label="" id="patronymic" defaultValue=""/>
            </FormControl>
            <FormControl>
                <DatePicker label="  *" id="date" required={true}/>
            </FormControl>
            <FormControl>
                <InputLabel required={true} htmlFor="email">E-mail</InputLabel>
                <OutlinedInput required={true} label="E-mail" id="email" defaultValue="email"/>
            </FormControl>
            <FormControl>
                <InputLabel required={true} htmlFor="tel"></InputLabel>
                <OutlinedInput required={true} label="" type="tel" id="tel" defaultValue="tel"/>
            </FormControl>
            <FormControl>
                <OutlinedInput type="submit"/>
            </FormControl>
        </form>
    );
}

export default PersonalData;